:root {
  --primary: #0a192f;
  --secondary: #64ffda;
  --accent: #8892b0;
  --text: #ccd6f6;
  --bg-card: rgba(17, 34, 64, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* BODY & TYPOGRAPHY */
body {
  background: var(--primary);
  color: var(--text);
  line-height: 1.6;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
}

h1, h2, h3, h4, h5 {
  font-family: 'Poppins', sans-serif;
  margin: 0;
}

/* NAVBAR */
.navbar {
  position: sticky;
  top: 0;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(136, 146, 176, 0.2);
  z-index: 1000;
  padding: 1rem 2rem;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* DEFAULT NAV LINKS (DESKTOP) */
.nav-links {
  display: flex;
  gap: 2rem;
}

.nav-link {
  color: var(--text);
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.9rem;
  transition: color 0.3s;
}
.nav-link:hover {
  color: var(--secondary);
}

/* SECTIONS */
section {
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

section h2 {
  color: var(--secondary);
  font-size: 2rem;
  margin-bottom: 2rem;
}

/* EXPERIENCE/PROJECT CARDS */
.experience-card {
  position: relative;
  background: var(--bg-card);
  border-radius: 10px;
  padding: 2rem;
  margin-bottom: 2rem;
  transition: transform 0.3s;
  padding-right: 4rem;
}
.experience-card:hover {
  transform: translateY(-5px);
}

/* SKILLS GRID */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
}
.skill-category {
  background: var(--bg-card);
  padding: 1.2rem;
  border-radius: 8px;
}
.skill-category h3 {
  color: var(--secondary);
  margin-bottom: 1rem;
  font-family: 'Poppins', sans-serif;
}
.skill-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin: 0.5rem 0;
  padding: 0.5rem;
  border-radius: 6px;
  transition: background 0.3s, transform 0.3s;
  font-family: 'Fira Code', monospace; /* Code-like feel for text */
}
.skill-item:hover {
  background: rgba(100, 255, 218, 0.1);
  transform: translateY(-3px);
}
.skill-icon {
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--secondary);
}

/* TECH STACK TAGS */
.tech-stack {
  margin-bottom: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.tech-tag {
  background: rgba(100, 255, 218, 0.1);
  color: var(--secondary);
  border: 1px solid var(--secondary);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
}

/* BUTTONS */
.btn {
  display: inline-block;
  padding: 0.6rem 1rem;
  border-radius: 4px;
  text-decoration: none;
  color: var(--primary);
  font-weight: 600;
  transition: background 0.3s, color 0.3s;
}
.btn-primary {
  background: var(--secondary);
}
.btn-primary:hover {
  filter: brightness(85%);
}
.btn-secondary {
  background: var(--accent);
  color: var(--primary);
}
.btn-secondary:hover {
  filter: brightness(85%);
}

/* RESPONSIVE DESIGN - MOBILE */
@media (max-width: 768px) {
  .nav-toggle {
    display: block;
  }
  .nav-content {
    position: relative;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  /* Hide nav-links by default on mobile */
  .nav-links {
    display: none;
  }
  /* When active, show them full-screen */
  .nav-links.active {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--primary);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    z-index: 999;
  }

  section {
    padding: 2rem 1rem;
  }
  .skills-grid {
    grid-template-columns: 1fr;
  }
  .experience-card {
    padding: 1.5rem;
  }
}

/* HAMBURGER (MOBILE) */
.nav-toggle {
  display: none;
  background: transparent;
  border: none;
  color: var(--text);
  font-size: 1.5rem;
  cursor: pointer;
}

/* ANIMATIONS */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
.animate-fadein {
  animation: fadeIn 0.6s ease-out forwards;
  opacity: 0;
  transform: translateY(20px);
}

/* HALO EFFECT FOR CARDS */
.experience-card {
  position: relative;
  overflow: hidden;
}
.experience-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent 25%,
    rgba(100, 255, 218, 0.1) 50%,
    transparent 75%
  );
  opacity: 0;
  transition: opacity 0.3s, transform 0.6s;
  transform: translate(0, 0) rotate(0deg);
  pointer-events: none;
}
.experience-card:hover::before {
  opacity: 1;
  transform: translate(50%, 50%) rotate(15deg);
}

/* FOOTER */
footer {
  text-align: center;
  color: var(--accent);
  font-size: 0.8rem;
  padding: 1rem;
  border-top: 1px solid rgba(136, 146, 176, 0.2);
  margin-top: 2rem;
}